@charset "UTF-8";
/*
 * Główny plik dla stylów.
 * Wszystkie partials podłączamy tutaj.
 */
:root {
  --vdarkpink: #d31342;
  --pinkbtnhover: #c7133e;
  --greybtnhover: #ecebeb;
  --buttonhome: #d31342;
  --pinkicon: #fea5bb;
  --darkpink: #d41443;
  --pink: #ffa5ba;
  --lightpink: #ffb8ca;
  --modalpink: #f4e6e9;
  --backgroundpink: #fff5f6;
  --white: #fff;
  --lgrey: #fafafa;
  --grey: #d9d9d9;
  --textgrey: #907e82;
  --black: #000;
  --beige: #f0d1a5;
  --mint: #c2e297;
  --modalmenucolor: #e17992;
  --footerborder: #e1e1e1;
  --desktop: 1200px;
  --tablet: 768px;
  --mobile: 320px;
  --cubic-bezier: cubic-bezier(0.4, 0, 0.2, 1);
  --titan: "Titan One";
  --shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
}

body {
  font-family: "DM Sans", sans-serif;
  font-weight: 400;
  color: var(--black);
}

li,
ul {
  list-style: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .container {
    padding-left: 34px;
    padding-right: 34px;
  }
}
.logo {
  margin-top: 6px;
  width: 199px;
  height: 50px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .logo {
    margin-top: 37px;
    width: 199px;
    height: 50px;
  }
}
.header-box {
  position: relative;
  background-color: var(--lightpink);
  background-image: url("../images/bigice.png"), url("../images/ellipse.png");
  background-repeat: no-repeat;
  background-position: left 117px top 94px, left 36px top 83px;
}
