  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.header-box__text--item {
  font-size: 48px;
  line-height: 0.96;
  color: var(--darkpink);
}

.milk-depiction {
  display: none;
}

.nr-position {
  display: none;
}

