    background-position: left 378px top 62px, left 566px top 265px, left 34px top 250px, left 307px top 30px;
  }
  .header-box__text {
    font-weight: 400;
    font-style: normal;
    max-width: 162px;
    margin-top: 15px;
    color: var(--white);
