    font-size: 22px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  }
  .header-box__text--item {
    font-size: 37px;
    line-height: 0.96;
    color: var(--darkpink);
  }
  .milk-depiction {
