  .header-number__text--nr {
    z-index: 2;
    font-family: var(--titan);
    font-style: normal;
    font-size: 28px;
    line-height: 32px;
    text-align: right;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--darkpink);
  }
  .header-number2 {
    margin-top: 13px;
  }
  .header-number2__dot {
    position: absolute;
    left: 35px;
    top: 13px;
    margin: 0;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    background-color: var(--white);
  }
  .header-number2__text {
    display: flex;
    flex-direction: column;
    font-family: "DM Sans";
    font-style: normal;
    font-size: 8px;
    line-height: 10px;
    text-align: right;
    color: var(--white);
  }
  .header-number2__text--nr {
    z-index: 2;
    font-family: var(--titan);
    font-style: normal;
    font-size: 28px;
    line-height: 32px;
    text-align: right;
    letter-spacing: 0.04em;
    text-transform: uppercase;
