  }
  .nr-position {
    position: absolute;
    top: 94px;
    left: 681px;
    display: flex;
    flex-direction: column;
  }
  .nr-box {
    position: relative;
  }
}
.btn-box {
  margin-top: 19px;
  padding-bottom: 160px;
  display: flex;
  align-items: center;
  text-align: center;
}
.btn-box__red, .btn-box__white {
  border-radius: 50px;
  margin-right: 8px;
  padding-left: 15.5px;
  padding-right: 15.5px;
  font-style: normal;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25;
  border: 0;
  height: 24px;
  background-color: var(--darkpink);
  color: var(--white);
}
.btn-box__white {
  background-color: var(--white);
  color: var(--darkpink);
}

.button-text {
  display: none;
}

.button-buy {
  display: none;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .btn-box {
    margin-top: 20px;
    padding-bottom: 160px;
  }
  .button-text {
    display: block;
    position: relative;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    border: 0px;
    margin-top: 7px;
  }
  .button-text__icon {
    position: absolute;
    top: 5.5;
    left: 5.5;
    fill: var(--darkpink);
  }
  .button-buy {
    position: absolute;
    left: 574px;
    top: 38px;
    border-radius: 50px;
    border: 0px;
    width: 160px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .button-buy__text {
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: var(--darkpink);
    margin-right: 12px;
  }
}
.about {
  padding-top: 125px;
  padding-bottom: 39px;
  background-color: var(--backgroundpink);
  background-image: url(../images/cowmilkabout.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 80%;
}
@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .about {
    background-image: url(../images/cowmilkabout@2x.png);
  }
}

.about__cow {
  display: flex;
  justify-content: center;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 46px;
  width: 280px;
  height: 279px;
  background-image: url(../images/cowmilk.png);
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .about__cow {
    background-image: url(../images/cowmilk@2x.png);
  }
}

.about__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about__content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 30px;
}

.about__header {
  font-family: var(--titan);
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
}
.about__header--label {
  font-size: 18px;
  line-height: 21px;
  color: var(--pink);
  margin-bottom: 15px;
}
.about__header--title {
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  color: var(--darkpink);
  margin-bottom: 26px;
}

.about__description {
  font-weight: 500;
  margin-bottom: 35px;
  font-size: 12px;
  line-height: 162%;
}

.about__description--primary {
  color: var(--black);
}

.about__description--secondary {
  color: var(--textgrey);
}

.button--about {
  background-color: var(--white);
  color: var(--darkpink);
  transition-property: background-color;
  transition-duration: 0.3s;
  transition-timing-function: var(--cubic-bezier);
  display: flex;
  gap: 10px;
  padding: 0 37;
  margin: 0;
  border: 0;
  border-radius: 22px;
  height: 44px;
  width: 176px;
  line-height: 1.3;
  font-weight: 700;
  font-size: 16px;
  justify-content: flex-start;
  align-items: center;
}
.button--about::after {
  content: "";
  width: 8px;
  height: 10px;
}
.button--about:hover, .button--about:focus {
  transition: fill 250ms var(--cubic-bezier);
  cursor: pointer;
  background-color: var(--greybtnhover);
}

.customer-review {
  padding: 80px 20px;
  justify-content: center;
  max-width: 100%;
  min-width: 320px;
}
.customer-review__list {
  list-style: none;
}
.customer-review__item {
  flex-direction: column;
  gap: 30px;
  align-items: center;
  display: none;
}
.customer-review__item.active {
  display: flex;
}
.customer-review__text-container {
  padding: 40px 62px 25px 62px;
  background-color: #fafafa;
  text-align: center;
  position: relative;
}
.customer-review__text-container::after {
  background-image: url(../images/review-dots.png);
  content: "";
  width: 32px;
  height: 4px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -25px;
  right: 3px;
}
.customer-review__text {
  flex-direction: column;
  background: repeating-linear-gradient(180deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 29px, #e1e1e1 29px, #e1e1e1 30px);
  position: relative;
  z-index: 2;
  font-size: 12px;
  line-height: 31px;
}
.customer-review__text:before {
  background-image: url(../images/review-quote.png);
  content: "";
  z-index: -1;
  width: 54px;
  height: 40px;
  position: absolute;
  top: -10px;
  left: 15px;
}
.customer-review__person {
  flex-direction: column;
}
.customer-review__person-name {
  font-size: 14px;
  padding-top: 18px;
}
.customer-review__slide-dots {
  padding-top: 30px;
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 18px;
}
.customer-review__slide-dots li {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #d9d9d9;
  cursor: pointer;
}
.customer-review__slide-dots li svg {
  display: none;
}
.customer-review__slide-dots li.is-active svg {
  display: block;
}
.customer-review__icon {
  width: 18px;
  height: 18px;
  background-color: white;
  fill: --buttonhome;
  padding-bottom: 6px;
}

